import './Appli.scss';
import logo from '../images/memo-logo.png';
import Controle from './Controle';
import Taches from './Taches';
import Accueil from './Accueil';

import Utilisateur from './Utilisateur';
import { observerEtatConnexion } from '../code/user';

// Fonctionnalits requises
import { useState, useEffect } from 'react';

export default function Appli() {
  
  // tat 'utilisateur'
  const [utilisateur, setUtilisateur] = useState(null);

  // tat des 'dossiers' de l'utilisateur connect
  const [dossiers, setDossiers] = useState([]);
  
  // Surveiller l'tat de la connexion Firebase Auth
  useEffect(() => observerEtatConnexion(setUtilisateur),[]);



  return (
    // 1)  Si un utilisateur est connect :
    utilisateur ?
        <div className="Appli">
          <header className='appli-entete'>
            <img src={logo} className="appli-logo" alt="Memo" />
          <Utilisateur utilisateur={utilisateur}/>
          </header>
          <Taches utilisateur={utilisateur} dossiers={dossiers} setDossiers={setDossiers}/>
          <Controle />
        </div>

        
      // 2) Par contre si aucun utilisateur n'est connect, on affiche plutt le composant suivant :
      :
        <Accueil /> 
  ); 
}
